@charset "UTF-8";
.carrito-resumen {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  background-color: #fff;
  box-shadow: -2px 0 5px rgba(0, 0, 0, 0.2);
  height: 100%;
  overflow-y: auto;
  padding: 20px;
}

.carrito-resumen h2 {
  margin-top: 0;
}

.carrito-resumen ul {
  list-style: none;
  padding: 0;
}

.carrito-resumen li {
  margin-bottom: 10px;
}

.carrito-resumen button {
  display: block;
  background-color: #8DB1AB;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  margin-top: 20px;
  border-radius: 5px;
}

