/* Estilos para el bot贸n "X" */
#cerrar-carrito.cerrar-carrito-btn {
  position: absolute;
  top: 2px;
  right: 5px;
  background: none;
  border: none;
  font-size: 1.5em;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.7); /* Color casi transparente (negro en este caso) */
}

/* Estilos para el encabezado del contenedor */
.carrito-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}